@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');

:root{
  --primary: #efb6b2;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  color: var(--secondary);
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
.title h1{
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
form{
  margin: 30px auto 10px;
  text-align: center;

}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}

.upload{
  height: 40px;
}
.upload:hover{
  cursor: pointer;
}

.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: var(--error);
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: rgb(137, 137, 137);
  margin-top: 20px;
}

/* image grid styles */
.img-grid{
  margin: 20px 50px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 26px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}

.borrar{
  position: absolute;
  top: 5px;
  right: 5px;
  z-index: 10;
  font-weight: bolder;
}

#cross:hover{
  cursor: pointer;
  height: 30px;
  width: 30px;
  top: 2.5px;
  right: 2.5px;
}

#cross{
  z-index: 1;
  position: absolute;
  top: 5px;
  right: 5px;
  left: auto;
  height: 16px;
  width: 16px;
  min-width: 0;
  min-height: 0;
  transition: 0.3s ease;
}